<template>
  <div class="page" id="record">
    <mt-header class="bar-nav" title="资金明细" >
      <mt-button slot="left" icon="back" v-back-link></mt-button>
    </mt-header>
    <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul class="record-list">
          <div v-for="item in newList">
            <div class="record-date">{{item.timeF}}</div>
            <li v-for="item2 in item.item">
              <span class="text-left color-333 fz-16">{{item2.funName}}</span>
              <span v-if="item2.moneyStr == '+'" class="text-right main-color fz-16">{{item2.moneyStr}}{{item2.money | currency('', 2)}}</span>
              <span v-else :class="{'color-999':item2.moneyStr == ''}" class="text-right color-green fz-16">{{item2.moneyStr}}{{item2.money | currency('', 2)}}</span>
              <span class="text-left color-999 fz-13">{{item2.createTime | dateFormatFun(4)}}</span>
              <span class="text-right color-999 fz-13 ellipsis">{{item2.accountTypeStr}}</span>
            </li>
          </div>
        </ul>
        <div class="text-center no-data" v-show="noData">
          <img src="../../assets/images/public/default/default_icon_no_record.png" alt="缺省图片">
          <p>暂无记录</p>
        </div>
      </mt-loadmore>
    </div>
  </div>
</template>
<script>
  import * as ajaxUrl from '../../ajax.config'
  export default {
    created(){
      this.projectList()
      this.$nextTick(function () {
        this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
        setTimeout(() => {
          var els = document.getElementsByClassName('record-date')
          var top_arr = []
          for(var i = 0; i < els.length; i++){
            top_arr.push(els[i].offsetTop)
          }
//          window.addEventListener('scroll', function () {
//            var sy = window.scrollY
//            for(var j = 0; j < top_arr.length; j++){
//              var sy2 = 25*j + sy
//              sy2 >= top_arr[j] ? els[j].classList.add('vux-fixed') : els[j].classList.remove('vux-fixed')
//            }
//          })
        }, 1000)
      })
    },
    methods: {
      projectList( page = 1){
        this.noData = false;
        this.$indicator.open({spinnerType: 'fading-circle'})
        let getParams = {
          userId: this.$store.state.user.userId,
          __sid: this.$store.state.user.__sid,
          'page.page': page
        }
        this.$http.get(ajaxUrl.logsDetail, {params: getParams}).then((res) => {
          this.$indicator.close()
          if(res.data.resData.list.length == 0){this.noData = true;}// 数据为空判断
          if(page > res.data.resData.totalPage){
            if(res.data.resData.totalPage > 0){
              this.$toast('无更多数据加载哦~')
            }
            return ; // 请求页大于总页数就返回不执行
          }
          let rawList = res.data.resData.list
          var index = 0;
          rawList.forEach(val => {
            let y = new Date(val.createTime).getFullYear()
            let m = new Date(val.createTime).getMonth() + 1
            let timeF =  y + '年' + m +'月'
            if(this.obj.hasOwnProperty(timeF)){
              this.newList[this.obj[timeF]].item.push(val)
            }else{
              this.obj[timeF] = index++
              this.newList.push({
                timeF: timeF,
                item: [val]
              })
            }
          })
          // this.list = this.list.concat(this.newList)
          this.totalPage = res.data.resData.totalPage
        })
      },
      loadBottom(id) {
        setTimeout(() => {
          let page = ++this.page
          this.projectList(page)
          this.$refs.loadmore.onBottomLoaded(id);
          if(page > this.totalPage){
            this.allLoaded = true;// 若数据已全部获取完毕
          }
        }, 500);
      }
    },
    data(){
      return {
        list: [],
        noData: false,
        allLoaded: false,
        page: 1,
        totalPage:1,
        obj: {},
        newList: [],
      }
    }
  }
</script>
<style scoped>
  .vux-sticky {
    width: 100%;
    position: sticky;
    top: 0;
  }
  .vux-fixed {
    width: 100%;
    position: fixed;
    top: 0;
  }
  .record-title{
    float: left;
    width: 100%;
    padding: 0 .15rem;
  }
  .record-title li{
    float: left;
    width: 50%;
    height: .45rem;
    line-height: .45rem;
    color: #666;
  }
  .text-left{  text-align: left;  }
  .text-right{  text-align: right;  }
  .record-list li{
    height: .7rem;
    padding: 0 .15rem;
    border-bottom: 1px solid #DDD;
    background: #fff;
  }
  .record-list li span{
    width: 50%;
    display: inline-block;
    float: left;
    line-height: 1;
    padding-top: .14rem;
  }
  .rd-tab-title li{
    width: 50%;
  }
  .record-date{
    line-height: .3rem;
    color: #999;
    padding: 0 5%;
    background:#f8f8f8;
  }
  /*加载更多组件样式*/
  @component-namespace page {
    @component loadmore {
      @descendent wrapper {
        overflow: scroll;
      }
      .mint-spinner {
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
  @component mint-loadmore-bottom {
    span {
      display: inline-block;
      transition: .2s linear;
      vertical-align: middle;
    @when rotate {
      transform: rotate(180deg);
    }
  }
  }
</style>



